import React from 'react'
import { useState } from 'react'
import api from '../utility';
function ContactUs() {
    const [email,setEmail] = useState("");
    const [subject,setSubject] = useState("");
    const [message,setMessage] = useState("");
    const emailChanger = (e) => {
        setEmail(e.target.value);
    }
    const subjectChanger = (e) => {
        setSubject(e.target.value);
    }

    const messageChanger = (e) => {
        setMessage(e.target.value);
    }

    const sendFeedback = async () => {
        try {
            await api.post("/sendFeedback",{
                email: "milangupta95@gmail.com",
                message: message,
                subject: subject
            });
            await api.post("/sendFeedback",{
                email: email,
                message: "Thanks For Your Feedback",
                subject: "Feedback Thanks"
            });
            window.alert("Feedback Has Been Send Successfully");
        } catch(err) {
            window.alert("There is Some Error");
        }
    }
    return (
        <section class="bg-white dark:bg-gray-900">
            <div class="py-8 lg:py-16 px-4 mx-auto max-w-screen-md">
                <h1 className='text-center text-2xl text-white'>Contact Us</h1>
                
                <form action="#" class="space-y-8">
                    <div>
                        <label for="email" class="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300">Your email</label>
                        <input value = {email} onChange = {emailChanger} type="text" id="email" class="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-primary-500 focus:border-primary-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500 dark:shadow-sm-light" placeholder="user@example.com" required/>
                    </div>
                    <div>
                        <label for="subject" class="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300">Subject</label>
                        <input value = {subject} onChange = {subjectChanger} type="text" id="subject" class="block p-3 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 shadow-sm focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500 dark:shadow-sm-light" placeholder="Let us know how we can help you" required/>
                    </div>
                    <div class="sm:col-span-2">
                        <label for="message" class="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-400">Your message</label>
                        <textarea value = {message} onChange = {messageChanger} id="message" rows="6" class="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg shadow-sm border border-gray-300 focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500" placeholder="Leave a comment..."></textarea>
                    </div>
                    <button onClick = {sendFeedback} type="submit" class="py-3 px-5 text-sm font-medium text-center text-white rounded-lg bg-primary-700 sm:w-fit hover:bg-primary-800 focus:ring-4 focus:outline-none focus:ring-primary-300 dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-primary-800">Send message</button>
                </form>
                <h1 className = 'text-center text-1xl text-white'> This Site is Managed By Milan Infotech</h1>
            </div>
        </section>
    )
}

export default ContactUs